

.custom-card {
  background-color: $card-bg;
}
.card-btn{
  //background-color: $btn-bg;
  color: $btn;
  border: none;
  border-radius: 5px;
  padding: 5px 10px;
  font-size: 1rem;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.3s ease-in-out;
  &:hover {
    background-color: $btn;
    color: $btn-bg;
    border: none;
    border-radius: 5px;
    padding: 5px 10px;
    font-size: 1rem;
    font-weight: 500;
    cursor: pointer;
    transition: all 0.3s ease-in-out;
  }
}
